document.addEventListener("DOMContentLoaded", function () {
    if (preLoaderEl) {
        setTimeout(function () {
            preLoaderEl.classList.add('hide');
            mainContent.classList.remove('overflow-hidden');
            mainContent.classList.remove('opacity-0');
            mainContent.classList.add('opacity-100');
            window.scroll({
                top: 0,
                left: 0,
                behavior: 'smooth'
            });
        }, 1300)
    }
    setTimeout(function(){
        headerBarAnimate.play();
        homepageHeroSliderAnimated.play();
	}, 1500);
   
})
// videos
const shortVideo = document.getElementById('orely-video-short');
const longVideo = document.getElementById('orely-video');
const popupVideoWrapper = document.querySelector('.video-popup-wrapper');
let videoIsPlay = false;
const buttonPlayFullVideo = document.querySelector('.orely-full-video-button');
const buttonCloseFullVideo = document.querySelector('.button-close-popup');
function videoPlay(el) {
    if (el) {
        videoIsPlay = true;
        el.play();
    } else {
        videoIsPlay = false;
    }
}
function videoPause(el) {
    videoIsPlay = false;
    if (el) {
        el.pause();
    }
}
const pvContent = document.querySelector('h3.pv-section__content');
var pvContentAnimate = gsap.timeline({
    smoothChildTiming: true,
    scrollTrigger: {
        // markers: true,
        trigger: pvContent,
        start: "top 70%",
        end: "bottom center"
    },
    defaults: {
        duration: 0.5
    }
});
pvContentAnimate
    .fromTo(
        pvContent, {
            opacity: 0
        }, {
            opacity: 1,
            duration: 0.5
        }
    )
    .fromTo(
        pvContent, {
            scale: 1.2
        }, {
            scale: 1,
            duration: 1
        },
        "<.1"
    );
function isVideoHalfVisible() {
    var rect = shortVideo.getBoundingClientRect();
    var windowHeight = window.innerHeight;
    return rect.top < windowHeight / 3 && rect.bottom > windowHeight / 3;
}
var options = {
    autoplay: 'muted',
    muted: true,
    loop: true,
    poster: document.getElementById('vs-poster').value,
    preload: 'metadata',
};
var promise;
var player = videojs('orely-video-short', options, function onPlayerReady() {
    this.on('ended', function () {
    });
});
document.addEventListener('scroll', function () {
    if (shortVideo) {
        if (promise === undefined && !videoIsPlay) {
            promise = player.play()
            if (promise !== undefined) {
                videoIsPlay = true;
            }
        }
        if (promise !== undefined) {
            promise.then(function () {
                if (document.querySelector('.orely-full-video-button').classList.contains('orely-full-video-button__hidden')) {
                    document.querySelector('.orely-full-video-button').classList.remove('orely-full-video-button__hidden');
                }
                if (isVideoHalfVisible()) {
                    if (!videoIsPlay) {
                        player.play();
                        videoIsPlay = true;
                    }
                } else {
                    if (videoIsPlay) {
                        player.pause();
                        videoIsPlay = false;
                    }
                }
            }).catch(function (error) {
                // Autoplay was prevented.
                document.querySelector('.orely-full-video-button')
                    .classList.add('orely-full-video-button__hidden');
            });
        }
    }
})
if (buttonPlayFullVideo) {
    buttonPlayFullVideo.addEventListener('click', function () {
        if (videoIsPlay || !shortVideo.paused) {
            videoPause(shortVideo);
        }
        if (popupVideoWrapper && longVideo) {
            popupVideoWrapper.classList.remove('popup_video_hide');
            popupVideoWrapper.classList.add('popup_video_show');
            document.querySelector('body').classList.add('overflow-hidden');
            videoPlay(longVideo);
        }
    })
}
if (buttonCloseFullVideo) {
    buttonCloseFullVideo.addEventListener('click', function () {
        popupVideoWrapper.classList.remove('popup_video_show');
        popupVideoWrapper.classList.add('z-[100]');
        popupVideoWrapper.classList.add('popup_video_hide');
        document.querySelector('body').classList.remove('overflow-hidden');
        longVideo.currentTime = 0;
        videoPause(longVideo);
        if (window.safari === undefined) {
            videoPlay(shortVideo);
        }
        setTimeout(function () {
            popupVideoWrapper.classList.remove('z-[100]');
            popupVideoWrapper.classList.add('-z-10');
        }, 2000);
    });
}
