const serviceCard = document.querySelectorAll('.card-service');
serviceCard.forEach((serviceItem, index) => {
    const serviceAnimate = gsap.timeline({
        paused: true, // Atur menjadi true agar animasi tidak dijalankan secara otomatis
        defaults: {
            duration: 1,
            ease: "power2.out"
        },
    });
    serviceAnimate.fromTo(serviceItem, {
        scale: 1.2,
        opacity: 0,
    }, {
        scale: 1,
        opacity: 1,
        stagger: 0.1,
    }, "<.2");
    const observer = new IntersectionObserver((entries, observer) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                serviceAnimate.play(); 
                observer.unobserve(serviceItem); 
            }
        });
    });
    observer.observe(serviceItem);
});
// Timeline section featuredworks
var featureWorkAnimate = gsap.timeline({
    scrollTrigger: {
        trigger: '.feature-work',
        start: 'top 80%',
    },
    smoothChildTiming: true
});
featureWorkAnimate
    .fromTo('.section-title', {
        opacity: 0,
        scale: 1.2
    }, {
        opacity: 1,
        scale: 1,
        duration: 1
    });
const featureWorks = document.querySelectorAll('.feature-work-lists .our-work__portofolio');
featureWorks.forEach(element => {
    var timelineFeaturedWorks = gsap.timeline({
        paused: true, // Atur menjadi true agar animasi tidak dijalankan secara otomatis
        defaults: {
            duration: 1,
            ease: "power2.out"
        },
        smoothChildTiming: true
    });
    timelineFeaturedWorks

        .fromTo(element.children[0], {
            width: '75%',
            duration: 1,

        }, {
            width: '100%',
            stagger: .2
        }, '<.1')
        .fromTo(element.children[0].children[0], {
            scale: 1.2
        }, {
            duration: 1,
            scale: 1,
            stagger: .2
        }, '-=1');

        const observer = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    timelineFeaturedWorks.play(); 
                    observer.unobserve(element); 
                }
            });
        }); 
       observer.observe(element);
});
